.container {
  /* height: 32vh; */
  /* width: 32vw; */
  color: #fff;
  font-family: "Courier", Times, serif;
  /* position: relative; */
  --img-height: 280px;
  display: flex;
  flex-direction: column;
}

.img {
  width: calc(var(--img-height) * 1.75);
  height: var(--img-height);
  border-radius: 8px;
  cursor: pointer;
  border: 1px solid #fff;
  box-sizing: border-box;
  position: absolute;
}

.img:hover {
  transition: 0.2s ease-in-out;
  opacity: 0;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 15px;
  font-weight: 400;
  width: calc(var(--img-height) * 1.75);
  height: var(--img-height);
  text-align: left;
  padding: 20px 32px;
  box-sizing: border-box;
  border-radius: 8px;
  background-color: #d5d5d3;
  color: #29211f;
  text-decoration: none;
  line-height: 20px;
}

.title {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 6%;
  text-align: center;
}

